
:root {
  --android-green: #2196f3;
}
.v-range-slider {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  /*padding: 2px 11px;*/
}
.v-range-slider input[type=range] {
  -webkit-appearance: none;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 16px 1px 20px;
  box-sizing: border-box;
  outline: none;
  background-color: transparent;
  -webkit-tap-highlight-color: transparent;
}
.v-range-slider input::-webkit-slider-runnable-track {
  position: relative;
  height: 2px;
  background: #BDBDBD;
  border: none;
  border-radius: 3px;
}
.v-range-slider input::-webkit-slider-thumb {
  -webkit-appearance: none;
  position: relative;
  margin-top: -12px;
  cursor: pointer;
  width: 14px;
  height: 14px;
  border: 0;
  border-radius: 50%;
  background: white;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.4);
}
.v-range-slider input::-webkit-slider-thumb:before {
  position: absolute;
  display: inline-block;
  content: '';
  top: 12px;
  left: -2001px;
  width: 2000px;
  height: 2px;
}
.v-range-slider input::-webkit-slider-thumb:before {
  background: var(--android-green);
  top: 9px;
}
.v-range-slider input::-webkit-slider-thumb {
  background: var(--android-green);
  border: 0;
  box-shadow: none;
  width: 14px;
  height: 14px;
  margin-top: -5px;
}



